.app {
  left: 0;
  top: 0;
  position: fixed;
  width: 100vw;
  height: 100vh;
  box-sizing: border-box;
  overflow: hidden;
}

@keyframes float-up {
  100% {
    top: -100vh;
  }

  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

.reward {
  position: absolute;
  color: rgba(100, 200, 50, 0.5);
  font-size: 5vh;
  animation: float-up 3s;
  z-index: 1000;
  pointer-events: none;
  user-select: none;
}

.stats {
  user-select: none;
  position: fixed;
  left: 0;
  top: 0;
  padding: 10px;

  .panel {
    padding: 10px;
    color: rgba(255, 255, 255, 0.9);
    background: rgba(0, 0, 0, 0.9);
  }
}